            </div>
            <div v-if="key === 'dining'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
            </div>
            <div v-if="key === 'service'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
            </div>
            <div v-if="key === 'slogan1'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
