                </p>
              </div>
            </div>
            <div v-if="key === 'foundation'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
            </div>
            <div v-if="key === 'healthcare'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
            </div>
            <div v-if="key === 'accommodation' || key === 'aboutAccommodation'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
            </div>
            <div v-if="key === 'communication'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
            </div>
            <div v-if="key === 'invoice'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
