                />
              </div>
            </div>
            <div v-if="key === 'vertical_shows'">
              <div style="display: flex">
                <img
                  v-for="(item, index) in value"
                  :key="index"
                  :src="item.url || item"
                  style="margin-right: 20px; height: 100px"
                  @load="imageLoad"
                />
              </div>
            </div>
            <div v-if="key === 'price_description'">
              <p v-for="(item, index) in value" :key="index">{{ item }}</p>
            </div>
            <div v-if="key === 'prepare_instruction'">
              <div v-for="(item, index) in value" :key="index">
                <p style="font-weight: bold">{{ item.name }}</p>
                <div>
                  <p v-for="(item2, index) in item.content" :key="index">
                    {{ item2 }}
                  </p>
                </div>
              </div>
            </div>
            <div v-if="key === 'syllabus'">
              <div v-for="(item, index) in value" :key="index">
                <p style="font-weight: bold">{{ item.title }}</p>
                <div>
                  <p v-for="(item2, index) in item.details" :key="index">
                    {{ item2 }}
                  </p>
                </div>
              </div>
            </div>
            <div v-if="key === 'schedule'">
              <div v-for="(item, index) in value" :key="index">
                <p style="font-weight: bold">{{ item.date }}</p>
                <div>
                  <div
                    style="display: flex"
                    v-for="(item2, index) in item.content"
                    :key="index"
