      </table>
    </div>
    <!-- 服务供应商 -->
    <div v-if="workLineID === 1500">
      <table
        style="
          color: var(--color-pure-black);
          border-collapse: collapse;
          border: none;
          width: 100%;
        "
      >
        <tr
          style="
            border-top: 0.5px solid #ccc;
            border-left: 0.5px solid #ccc;
            border-right: 0.5px solid #ccc;
          "
        >
          <th
            style="padding: 6px 30px; font-size: 30px; background-color: #eee"
            colspan="2"
          >
            {{ processTitle(workLineID, workOperation) }}
          </th>
        </tr>
        <tr v-for="(value, key) in ticketContent" :key="key">
          <th
            style="
              border-right: 1px solid #000;
              padding: 6px 10px;
              border: 0.5px solid #ccc;
              width: 125px;
            "
          >
            {{ processKey(key) }}
          </th>
          <th
            style="
              padding: 3px 10px;
              font-weight: lighter;
              border: 0.5px solid #ccc;
            "
          >
            <p v-if="key !== 'portrait' && key !== 'avatar_square'">
              {{ processValue(key, value) }}
            </p>
            <div v-if="key === 'portrait' || key === 'avatar_square'">
              <img :src="value" style="width: 120px" @load="imageLoad" />
            </div>
            <div v-if="key === 'categories'">
              <div style="display: flex">
                <p
                  v-for="(item, index) in value"
                  :key="index"
                  style="margin-right: 20px"
                >
                  {{ findNameByID(item, categoriesGOT) }}
                </p>
              </div>
            </div>
            <div v-if="key === 'shows'">
              <div style="display: flex">
                <img
                  v-for="(item, index) in value"
                  :key="index"
                  :src="item.url || item"
                  style="margin-right: 20px; width: 100px"
                  @load="imageLoad"
                />
              </div>
            </div>
            <div v-if="key === 'honor'">
              <div style="display: flex">
                <img
                  v-for="(item, index) in value"
                  :key="index"
                  :src="item.url || item"
                  style="margin-right: 20px; height: 100px"
                  @load="imageLoad"
                />
              </div>
            </div>
          </th>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import { $getEnvInfo } from "../../../../../network/providerTools/activity";

export default {
  name: "e-TicketContent",
  props: {
    ticketContent: {
      type: Object,
      default: () => {},
    },
    workLineID: {
      type: Number,
      default: 1300,
    },
    workOperation: {
      type: Number,
      default: 4200,
    },
  },
  data() {
    return {
      activityModesGOT: [],
      categoriesGOT: [],
      featuresGOT: [],
    };
  },
  async created() {
    try {
      const response = await $getEnvInfo();
      if (response.Code === 200 && response.msg === "success") {
        this.activityModesGOT = response.details.activitymodes;
        this.categoriesGOT = response.details.categories;
        this.featuresGOT = response.details.features;
      }
    } catch (err) {
      this.$toast.error("基础配置信息获取失败，请尝试刷新页面", 0, 400);
    }
  },
  methods: {
    imageLoad() {
      this.$bus.$emit("itemImageLoad");
    },
    processTitle(workLineID, workOperation) {
      let str1, str2;
      switch (workLineID) {
        case 1200:
          str1 = "活动类别";
          break;
        case 1300:
          str1 = "活动特色";
          break;
        case 1400:
          str1 = "活动详情";
          break;
        case 1500:
          str1 = "主理人";
          break;
      }
      switch (workOperation) {
        case 4200:
          str2 = "添加";
          break;
        case 4300:
          str2 = "更新";
          break;
        case 4900:
          str2 = "删除";
          break;
      }
      return `${str1} ${str2}`;
    },
    processKey(keyName) {
      if (this.workLineID === 1400) {
        switch (keyName) {
          case "full_name":
            return "产品全称";
          case "short_name":
            return "产品简称";
          case "exclusive":
            return "是否独家";
          case "free":
            return "是否免费";
          case "welfare":
            return "是否是公益产品";
          case "nonprofit":
            return "是否非盈利产品";
          case "invoice":
            return "服务商向消费者开具的发票类型";
          case "insurance":
            return "保险类型";
          case "expenses_rate":
            return "服务费率";
          case "other_expenses":
            return "其他费用";
          case "oversold_limit":
            return "可超卖人数";
          case "size_valid":
            return "成团人数";
          case "recruit_total":
            return "总人数";
          case "staffing_ratio":
            return "工作人员比例";
          case "categories":
            return "活动类别";
          case "features":
            return "活动特色";
          case "age_min":
            return "最小招生年龄";
          case "age_max":
            return "最大招生年龄";
          case "activitymode_id":
            return "日程形式";
          case "theme_related":
            return "内容形式";
          case "difficulty":
            return "活动难易度";
          case "foundation":
            return "基础要求";
          case "activity_province":
            return "活动省份";
          case "location_province":
            return "活动省份";
          case "activity_city":
            return "活动城市";
          case "location_city":
            return "活动城市";
          case "activity_district":
            return "活动区县";
          case "location_district":
            return "活动区县";
          case "activity_detail":
            return "活动详细地点";
          case "location_street":
            return "活动详细地点";
          case "activity_start_time":
            return "活动开始时间";
          case "activity_end_time":
            return "活动结束时间";
          case "activity_days":
            return "活动天数";
          case "product_launching_time":
            return "预计活动上架时间";
          case "product_expiration_time":
            return "活动截止报名时间";
          case "promotion":
            return "是否优惠";
          case "price_original":
            return "原价";
          case "price_selling":
            return "现价";
          case "price_description":
            return "价格说明";
          case "refundpolicy_id":
            return "退款政策";
          case "dining":
            return "餐饮标准";
          case "accommodation":
            return "住宿管理";
          case "aboutAccommodation":
            return "住宿管理";
          case "communication":
            return "信息交互";
          case "healthcare":
            return "医疗保障";
          case "horizontal_shows":
            return "横板图片";
          case "vertical_shows":
            return "竖版图片";
          case "video_short":
            return "宣传视频";
          case "prepare_instruction":
            return "开营须知";
          case "slogan1":
            return "主宣传语";
          case "topic":
            return "产品主题";
          case "topic_intro":
            return "主题简介";
          case "slogan2":
            return "副宣传语";
          case "advantage1":
            return "产品优势1";
          case "advantage1_intro":
            return "产品优势1简介";
          case "advantage2":
            return "产品优势2";
          case "advantage2_intro":
            return "产品优势2简介";
          case "gain":
            return "收获内容";
          case "facility":
            return "硬件设施";
          case "service":
            return "服务";
          case "staffing":
            return "人员配置";
          case "syllabus":
            return "教学提纲";
          case "schedule":
            return "日程表";
          case "provider_id":
            return "服务商id";
          case "id":
            return "服务商id";
          default:
            return keyName;
        }
      } else if (this.workLineID === 1500) {
        switch (keyName) {
          case "id":
            return "主理人id";
          case "name":
            return "主理人名称";
          case "email":
            return "主理人邮箱";
          case "honor":
            return "荣誉资质";
          case "shows":
            return "主理人风采";
          case "mobile":
            return "主理人手机号";
          case "wechat":
            return "主理人微信";
          case "describe":
            return "简介";
          case "portrait":
            return "主理人形象照";
          case "exclusive":
            return "是否独家";
          case "categories":
            return "主理人类别";
          case "avatar_square":
            return "主理人头像";
          default:
            return keyName;
        }
      }
    },
    processValue(key, value) {
      const typeStr = Object.prototype.toString.apply(value);
      if (typeStr === "[object String]") {
        if (value) return value;
        else return null;
      } else if (typeStr === "[object Boolean]") {
        if (value) {
          return "是";
        } else {
          return "否";
        }
      } else if (typeStr === "[object Number]") {
        if (key === "activitymode_id") {
          return this.activityModesGOT.find((item) => item.id === value).name;
        } else {
          return value;
        }
      } else {
        return null;
      }
    },
    findNameByID(id, array) {
      if (Object.prototype.toString.apply(array) === "[object Array]") {
        return array.find((item) => item.id === id).name;
      } else {
        return "【无数据】";
      }
    },
  },
};
</script>

<style scoped>
.boardContent {
  padding-bottom: 20px;
}
</style>
