<template>
  <div id="reviewProgress">
    <div class="request-table">
      <div class="header">
        <div class="tableHeader">
          <p class="header1">创建日期</p>
          <p class="header2">内容模块</p>
          <p class="header3">版本</p>
          <p class="header4">申请理由</p>
          <p class="header5">状态</p>
        </div>
      </div>
      <Scroll class="content2" :probe-type="3" ref="scroll">
        <order-list :list="listShow"></order-list>
        <div class="paginationZone" v-show="listShow.length !== 0">
          <a-pagination
            :total="listAll.length"
            :show-total="
              (total, range) => `${range[0]}-${range[1]} of ${total} items`
            "
            :page-size="pageSize"
            :default-current="1"
            v-model:current="currentPage"
          />
        </div>
        <empty-data-placeholder
          v-if="listShow.length === 0"
          description="暂无工单数据"
        ></empty-data-placeholder>
      </Scroll>
    </div>

    <div class="fixedCoverPage" v-if="isShowCoverPage"></div>

    <!-- 工单详情查看弹框 -->
    <div class="absoluteScrollDialogStore">
      <div
        class="ticketContent"
        style="
          background-color: #000;
          width: 0px;
          height: 0px;
          margin-left: 10px;
          margin-bottom: 3px;
        "
      >
        <div
          class="details"
          v-show="viewBoardVisible"
          style="
            width: 0px;
            height: 0px;
            background-color: #fff;
            box-shadow: var(--color-text-grey) 0 0 7px;
            overflow: hidden;
            position: absolute;
            z-index: 9;
          "
        >
          <p
            class="imageZone"
            @click="hideViewDialog()"
            v-show="ifShowCancelIcon"
          ></p>
          <Scroll ref="roll" class="content3">
            <ticket-content
              :ticketContent="activeViewItem"
              :workLineID="workLineIDOfTicket"
              :workOperation="workOperationCodeOfTicket"
            ></ticket-content>
          </Scroll>
        </div>
      </div>
    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
  </div>
</template>

<script>
import Scroll from "../../../components/common/scroll/Scroll.vue";
import { $getListByUser } from "../../../network/adminTools/order";
import OrderList from "./childComponents/orderList/OrderList.vue";
import EmptyDataPlaceholder from "../../../components/common/emptyDataPlaceholder/EmptyDataPlaceholder.vue";
import TicketContent from "./childComponents/ticketContent/TicketContent.vue";
import $ from "jquery";
import { findDimensions, debounce } from "../../../common/utils";
