  /**
   * 组件内路由守卫
   */
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isProvider") === "true") {
      window.localStorage.setItem("currentIndex010006", -1);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("currentIndex010006");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
    }
  },
  beforeCreate() {
    if (window.localStorage.getItem("isProvider") === false) {
      this.$router.replace("/tools/login");
    }
  },
  /**
   * 每一次组件处于活跃状态时
   */
  async created() {
    if (this.$refs.scroll) {
      this.$refs.scroll.refresh();
    }
    this.isLoading = true;
    try {
      // const userid = Number(this.cookieOperations.getCookie('userId'));
      const requestPayload = {
        user: {
          id: Number(window.localStorage.getItem("userID")),
        },
      };
      const response = await $getListByUser(requestPayload);
      console.log("provider review res: ", response);
      const { Code, msg } = response;
      // 获取当前登录用户所有工单数据
      if (Code === 200 && msg === "success") {
        const { details } = response;
        for (const item of details) {
          item.date_created_timestamp = new Date(item.date_created).getTime();
        }
        //按时间戳降序排，即最新提交的在最前面
        details.sort(
          (a, b) => b.date_created_timestamp - a.date_created_timestamp
        );

        this.listAll = details; // 将数据赋值给listAll
        this.totalPage = Math.ceil(this.listAll.length / this.pageSize); // 重新计算totalPage
        const tempListAll = window.JSON.parse(
          window.JSON.stringify(this.listAll)
        );
        // 若listShow数组为空，则说明是首次加载组件
        if (this.listShow.length === 0) {
          this.listShow = tempListAll.slice(0, this.pageSize);
        }
        // 若进来页面时分页的currentPage和分页的totalPage相等，因为listAll的数据有可能进行了更新，所以需要更新listShow
        if (this.currentPage === this.totalPage) {
          this.listShow = tempListAll.slice(
            this.pageSize * (this.currentPage - 1),
            this.pageSize * this.currentPage
          );
        }
      } else {
        this.$toast.error("数据获取失败", 240);
      }
    } catch (err) {
      this.$toast.error("数据获取失败", 240);
    }
    this.isLoading = false;

    // 监听到此事件后，做打开工单详情查看弹框的操作
    this.$bus.$on("viewDetailsClick", (param) => {
      const { x, y, index } = param;

      this.x = x;
      this.y = y;
      const elex = this.x;
      const eley = this.y;
      this.activeViewItem = this.listShow[index].content;
      this.workLineIDOfTicket = this.listShow[index].work_line_id;
      this.workOperationCodeOfTicket = this.listShow[index].work_operation;
      const vm = this;
      console.log("vm: ", vm);
      const str = ".details";
      this.isShowCoverPage = true;
      // 计算第二个动画要移动到哪个点
      let targetLeft;
      const clientWidth = document.body.clientWidth;
      if (clientWidth < 1000) {
        targetLeft = 0;
      } else if (clientWidth > 1000 && clientWidth < 1240) {
        targetLeft = (clientWidth - 1000) / 2;
      } else {
        targetLeft = 240 + (clientWidth - 240 - 1000) / 2;
      }

      let targetTop;
      let clientHeight = document.body.clientHeight;
      if (clientHeight < 680) {
        targetTop = 0;
      } else {
        targetTop = (clientHeight - 680) / 2;
      }

      this.inAnimating = true;
      $(str).animate({ left: elex, top: eley }, 50, function () {
        vm.viewBoardVisible = true;
      });
      $(str).animate(
        { left: targetLeft, top: targetTop, width: "100px", height: "100px" },
        100,
        function () {
          // vm.$refs.roll.refresh();
          // vm.$refs.roll.scrollTo(0, 0, 0);
        }
      );
      $(str).animate({ width: "1000px", height: "680px" }, 150, function () {
        vm.inAnimating = false;
        vm.ifShowCancelIcon = true;
      });
    });

    //监听窗口高度/宽度的改变
    window.addEventListener("resize", () => {
      const desArr = findDimensions();
      setTimeout(() => {
        if (!this.inAnimating && this.viewBoardVisible) {
          // 计算查看ticket详情的dialog的left和top值
          if (desArr[0] < 1000) {
            this.contentDialogLeft = 0;
          } else if (desArr[0] > 1000 && desArr[0] < 1240) {
            this.contentDialogLeft = (desArr[0] - 1000) / 2;
          } else {
            this.contentDialogLeft = 240 + (desArr[0] - 240 - 1000) / 2;
          }

          if (desArr[1] < 680) {
            this.contentDialogTop = 0;
          } else {
            this.contentDialogTop = (desArr[1] - 680) / 2;
          }
          document.querySelector(".details").style.left =
            this.contentDialogLeft + "px";
          document.querySelector(".details").style.top =
            this.contentDialogTop + "px";
        }
      }, 100);
    });

    if (this.$refs.roll) {
      this.debounceRefresh = debounce(this.$refs.roll.refresh, 500);
    }
    //监听图片加载，实时更新Scroll组件的可滚动高度
    this.$bus.$on("itemImageLoad", () => {
      console.log("image is loading");
      this.debounceRefresh();
    });
