<template>
  <div class="list-item">
    <div class="order-content">
      <div class="date-created">{{ setDate(item.date_created) }}</div>
      <div class="content-module">{{ setModuleName(item.work_line_id) }}</div>
      <div class="version">V{{ item.version }}</div>
      <div class="create-reason">{{ item.create_reason }}</div>
      <div class="audit-status">
        <div class="status">{{ setStatus(item.status) }}</div>
        <div class="check" @click="viewDetailsClick" title="点击查看详情">
          查看
        </div>
      </div>
    </div>
    <div class="remark-content">
      <p class="placeHolder1" />
      <p class="remark">备注：{{ item.review_comment }}</p>
      <p class="placeHolder2" />
    </div>
  </div>
</template>

<script>
import { UrlDecode, processDate } from "../../../../../../common/utils";

export default {
  name: "e-ListItem",
  props: {
    item: {
      type: Object,
      default() {
        return {};
      },
    },
    index: {
      type: Number,
    },
  },
  data() {
    return {};
  },
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {
    VMUrlDecode(str) {
      return UrlDecode(str);
    },
    viewDetailsClick(e) {
      const param = {
        x: e.clientX,
        y: e.clientY,
        index: this.index,
      };
      this.$bus.$emit("viewDetailsClick", param);
    },
    setStatus(code) {
      if (code === 3200) {
        return "正在审核中";
      } else if (code === 3300) {
        return "审核通过";
      } else {
        return "审核不通过";
      }
    },
    setModuleName(code) {
      if (code === 1200) {
        return "活动类别";
      } else if (code === 1300) {
        return "活动特色";
      } else if (code === 1400) {
        return "产品详情";
      } else {
        return "主理人";
      }
    },
    setDate(text) {
      return processDate(text);
    },
  },
  components: {},
};
</script>

